import React from 'react';
import web from '../src/Images/IMG_0663.PNG';
import { NavLink } from 'react-router-dom';


const Navbar = () => {
    return (
        <>
            <div className="container-fluid nav_bg">
                <div className="row">
                    

                        <nav className="navbar navbar-expand-lg navbar-dark shadow bg-dark">
                            <NavLink className="navbar-brand " to="/"><img className="ml-lg-5" style={{  width: "50px" , height: "50px"}} src={web}/></NavLink>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>

                            <div className="collapse navbar-collapse mr-lg-5" id="navbarSupportedContent">
                                <ul className="navbar-nav ml-auto">
                                    <li className="nav-item active">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/" exact>Home <span className="sr-only">(current)</span></NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/service">Services</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/about">About</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="menu_active" className="nav-link" to="/contact">Contact</NavLink>
                                    </li>

                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            
        </>
    );

};
export default Navbar;